<template>
    <div class="hello">
        <h1>{{ msg }}</h1>
        <button @click="getGuid()">Send</button>
        <h3>{{ guid }}</h3>

    </div>
</template>

<script>
    import axios from "axios";

    export default {
        name: 'HelloWorld',
        props: {
            msg: String
        },

        data() {
            return {
                guid: ""
            };
        },
        
        methods: {
            getGuid() {
                let prefix = "";
                //prefix = "https://localhost:44363";
                axios.get(prefix + "/api/Guid/GetGuid/").then(result => this.guid = "Result: " + result.data);
            }
        }
    }
</script>

